import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import {VMS_URL} from "../Config/Config";


export default function EditPass ({passObj}) {

    const [sessionUserData, setsessionUserData] = useState([]);
    const [viewLoading, setViewLoading] = useState(false);
    const [viewData, setViewData] = useState([]);
    const [editData, setEditData] = useState({
        "orgid": sessionUserData.OrgId,
        "userid": 1,
        "Operation": "EDIT",
        "RequestPass": {
            "RequestId": "",
            "VisitorName": "",
            "Email": "",
            "Mobile": "",
            "CompanyName": "",
            "RequestDate": "",
            "MeetingDate": "",
            "IsVehicle": true,
            "VehicleInfo": "",
            "ReturnOfMaterials": "",
            "Remarks": "",
            "NoOfMembers": "",
            "VisitorType": "",
            "Status": "",
            "IsActive": 1
        },
        "Attendees": []
      });

      useEffect(() => {
        setEditData({"orgid": sessionUserData.OrgId,
            "userid": sessionUserData.UserId,
            "Operation": "EDIT",
            "RequestPass": {
                "RequestId": passObj?.RequestId,
                "VisitorName": passObj?.VisitorName,
                "Email": "",
                "Mobile": "",
                "CompanyName": "",
                "RequestDate": "",
                "MeetingDate": "",
                "IsVehicle": true,
                "VehicleInfo": "",
                "ReturnOfMaterials": "",
                "Remarks": "",
                "NoOfMembers": "",
                "VisitorType": "",
                "Status": "",
                "IsActive": 1
            },
            "Attendees": []}
        );
      }, [passObj]);
      

    useEffect(() => {
        const userDataString = sessionStorage.getItem('userData');
        if (userDataString) {
            const userData = JSON.parse(userDataString);
            setsessionUserData(userData);
        } else {
            console.log('User data not found in sessionStorage');
        }
    }, []);

    const fetchViewEditData = async () => {
        setViewLoading(true);
        try {
            const response = await fetch(`${VMS_URL}getReqPassById?RequestId=${passObj.RequestId}`);
            if (response.ok) {
                const data = await response.json();
                if (data.Status) {
                    setViewData(data.ResultData);
                    console.log(data.ResultData, 'edit data......')
                    console.log(passObj, 'passobj getting from list...');
                } else {
                    setViewData([]);
                }
            } else {
                console.error('Failed to fetch attendees data:', response.statusText);
            }
        } catch (error) {
            console.error('Error fetching attendees data:', error.message);
        } finally {
            setViewLoading(false);
        }
    };

    useEffect(() => {
        fetchViewEditData();
    }, [passObj.RequestId]);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setEditData((prevData) => ({
            ...prevData,
            RequestPass: {
                ...prevData.RequestPass,
                [name]: value,
            },
        }));
    };

    const handleAttendeeChange = (index, key, value) => {
        setEditData((prevData) => {
            const updatedAttendees = [...prevData.Attendees];
            updatedAttendees[index][key] = value;
            return { ...prevData, Attendees: updatedAttendees };
        });
    };

    const handleSubmit = async () => {
        try {
            const response = await fetch(`${VMS_URL}auth`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(editData),
            });

            if (response.ok) {
                const result = await response.json();
                console.log("Edit successful:", result);
            } else {
                console.error("Edit failed:", response.statusText);
            }
        } catch (error) {
            console.error("Error submitting data:", error.message);
        }
    };
      

    return (
        <div className="offcanvas offcanvas-end" tabIndex="-1" id="offcanvasRightEdit" aria-labelledby="offcanvasRightLabel"
            style={{ minWidth: '52%', maxWidth: '52%' }}
        >
            <div className="offcanvas-header">
                <h5>Edit Request</h5>
                <button type="button" className="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
            </div>
            {viewLoading ? <p className="text-center">Loading...</p> :
                <div className="offcanvas-body" style={{ marginTop: '-2rem' }}>
                    <div className="row">
                        <div className="col-6 mb-2">
                            <label className="form-label justify-content-start d-flex">Date</label>
                            <input 
                                type="datetime local"
                                className="form-control"
                                value={passObj?.MeetingDate}
                                onChange={handleChange}
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label justify-content-start d-flex">Attendee Name</label>
                            <input 
                                type="text"
                                className="form-control"
                                placeholder="Attendee name"
                                value={passObj?.VisitorName || "N/A"}
                                onChange={handleChange}
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label justify-content-start d-flex">Mobile No</label>
                            <input 
                                type="number"
                                className="form-control"
                                placeholder="Mobile number"
                                value={passObj?.Mobile}
                                onChange={handleChange}
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label justify-content-start d-flex">Email Id</label>
                            <input 
                                type="email"
                                name="Email"
                                className="form-control"
                                placeholder="Enter Email address"
                                value={passObj?.Email}
                                onChange={handleChange}
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label justify-content-start d-flex">Vehicle No</label>
                            <input 
                                type="text"
                                className="form-control"
                                placeholder="Vehicle number"
                                value={passObj?.VehicleInfo}
                                onChange={handleChange}
                            />
                        </div>
                        <div className="col-12 mb-2">
                            <label className="form-label justify-content-start d-flex">Remarks</label>
                            <textarea 
                                name="Remarks"
                                className="form-control" 
                                rows={3} 
                                placeholder="Notes.."
                                value={passObj?.Remarks}
                                onChange={handleChange}
                            ></textarea>
                        </div>
                    </div>
                    <h4 className={`text-start my-2 ${viewData?.visitors?.length > 0 ? 'd-block' : 'd-none'}`}>Attndees:</h4>
                    {viewData?.map((attendee, index) => (
                        <div key={index} className="row mb-2 align-items-center">
                            <div className="col-4">
                                <label>Name</label>
                            <input
                                type="text"
                                className="form-control"
                                value={attendee.AttendeName || ""}
                                onChange={(e) => handleAttendeeChange(index, "AttendeName", e.target.value)}
                            />
                            </div>
                            <div className="col-4">
                                <label>Phone</label>
                            <input
                                type="number"
                                className="form-control"
                                value={attendee.PhoneNumber || ""}
                                onChange={(e) => handleAttendeeChange(index, "PhoneNumber", e.target.value)}
                            />
                            </div>
                            <div className="col-4">
                                <label>Email</label>
                            <input
                                type="email"
                                className="form-control"
                                value={attendee.Email || ""}
                                onChange={(e) => handleAttendeeChange(index, "Email", e.target.value)}
                            />
                            </div>
                        </div>
                    ))}
                </div>
            }
        </div>
    )
};

EditPass.propTypes = {
    passObj: PropTypes.object.isRequired
};