import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { VMS_URL } from "../Config/Config";


export default function ViewVisit ({viewId}) {

    const [sessionUserData, setsessionUserData] = useState([]);
    const [viewLoading, setViewLoading] = useState(false);
    const [viewData, setViewData] = useState([]);

    useEffect(() => {
        const userDataString = sessionStorage.getItem('userData');
        if (userDataString) {
            const userData = JSON.parse(userDataString);
            setsessionUserData(userData);
        } else {
            console.log('User data not found in sessionStorage');
        }
    }, []);

    const fetchViewEditData = async () => {
        setViewLoading(true);
        try {
            const response = await fetch(`${VMS_URL}getReqPassById?RequestId=${viewId}`);
            if (response.ok) {
                const data = await response.json();
                if (data.Status) {
                    setViewData(data.ResultData);
                    console.log(data.ResultData)
                } else {
                    setViewData([]);
                }
            } else {
                console.error('Failed to fetch attendees data:', response.statusText);
            }
        } catch (error) {
            console.error('Error fetching attendees data:', error.message);
        } finally {
            setViewLoading(false);
        }
    };
    useEffect(() => {
        fetchViewEditData();
    }, [viewId]);

    const formatDate = (dateString) => {
        const date = new Date(dateString);
        const formattedDate = date.toLocaleDateString('en-GB');
        const formattedTime = date.toLocaleTimeString('en-GB', {
            hour: '2-digit',
            minute: '2-digit',
        });
        return `${formattedDate} ${formattedTime}`;
    };

    return (
        <div className="offcanvas offcanvas-end" tabIndex="-1" id="offcanvasRightView" aria-labelledby="offcanvasRightLabel"
            style={{ minWidth: '52%', maxWidth: '52%' }}
        >
            <div className="offcanvas-header">
                <h5>View Request</h5>
                <button type="button" className="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
            </div>
            {viewLoading ? <p className="text-center">Loading...</p> :
                <div className="offcanvas-body" style={{ marginTop: '-2rem' }}>
                    <div className="row">
                        <div className="col-6 mb-2">
                            <label className="form-label justify-content-start d-flex">Date</label>
                            <input 
                                type="text"
                                className="form-control"
                                value={formatDate(viewData[0]?.MeetingDate)}
                                readOnly
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label justify-content-start d-flex">Attendee Name</label>
                            <input 
                                type="text"
                                className="form-control"
                                placeholder="Attendee name"
                                value={viewData[0]?.VisitorName || "N/A"}
                                readOnly
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label justify-content-start d-flex">Mobile No</label>
                            <input 
                                type="number"
                                className="form-control"
                                placeholder="Mobile number"
                                value={viewData[0]?.Mobile}
                                readOnly
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label justify-content-start d-flex">Email Id</label>
                            <input 
                                type="email"
                                name="Email"
                                className="form-control"
                                placeholder="Enter Email address"
                                value={viewData[0]?.Email}
                                readOnly
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label justify-content-start d-flex">Vehicle No</label>
                            <input 
                                type="text"
                                className="form-control"
                                placeholder="Vehicle number"
                                value={viewData[0]?.VehicleInfo}
                                readOnly
                            />
                        </div>
                        <div className="col-12 mb-2">
                            <label className="form-label justify-content-start d-flex">Remarks</label>
                            <textarea 
                                name="Remarks"
                                className="form-control" 
                                rows={3} 
                                placeholder="Notes.."
                                value={viewData[0]?.Remarks}
                                readOnly
                            ></textarea>
                        </div>
                    </div>
                    <h4 className={`text-start my-2 ${viewData?.visitors?.length > 0 ? 'd-block' : 'd-none'}`}>Attndees:</h4>
                    {Array.isArray(viewData) && viewData.map((attendee, index) => (
                        <div className={`${viewData.length > 1 ? 'd-block' : 'd-none'}`}>
                            <div key={index} className={`row mb-2 align-items-center`}>
                                <div className="col-4">
                                    <label className="form-label justify-content-start d-flex">
                                        Name
                                    </label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        value={attendee.AttendeName}
                                        placeholder="Attendee name"
                                        readOnly
                                    />
                                </div>
                                <div className="col-4">
                                    <label className="form-label justify-content-start d-flex">
                                        Mobile
                                    </label>
                                    <input
                                        type="number"
                                        className="form-control"
                                        value={attendee.PhoneNumber}
                                        placeholder="Mobile no"
                                        readOnly
                                    />
                                </div>
                                <div className="col-4">
                                    <label className="form-label justify-content-start d-flex">
                                        Email
                                    </label>
                                    <input
                                        type="email"
                                        className="form-control"
                                        value={attendee.AttendeEmail}
                                        placeholder="Email id"
                                        readOnly
                                    />
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            }
        </div>
    )
};

ViewVisit.propTypes = {
    viewId: PropTypes.number.isRequired
};