import React, { useEffect, useState } from "react";
import Swal from 'sweetalert2';
import { VMS_URL_CONTRACTOR } from "../Config/Config";


export default function AddContactor () {

    const [sessionUserData, setsessionUserData] = useState([]);
    const [shiftsData, setShiftsData] = useState([]);
    const [addSubmitLoading, setAddSubmitLoading] = useState(false);

    useEffect(() => {
        const userDataString = sessionStorage.getItem('userData');
        if (userDataString) {
            const userData = JSON.parse(userDataString);
            setsessionUserData(userData);
        } else {
            console.log('User data not found in sessionStorage');
        }
    }, []);

    const [formData, setFormData] = useState({
        ContractorName: "",
        ShiftTypeId: "",
        StartDate: "",
        EndDate: "",
        RequestPasses: "",
        Remarks: "",
    });

    const fetchShiftsData = async () => {
        try {
            const response = await fetch(`${VMS_URL_CONTRACTOR}getShiftTimings?OrgId=${sessionUserData.OrgId}`);
            if (response.ok) {
                const data = await response.json();
                setShiftsData(data.ResultData);
            } else {
                console.error('Failed to fetch shifts data:', response.statusText);
            }
        } catch (error) {
            console.error('Error fetching shifts data:', error.message);
        } 
    };

    useEffect(() => {
        if (sessionUserData.OrgId) {
            fetchShiftsData();
        }
    }, [sessionUserData]);

    const handleContactorChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevState) => ({
            ...prevState,
            [name]: value,
        }));
    };
   
    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            setAddSubmitLoading(true);
            formData.UserId = sessionUserData.UserId

            const payload = {
                OrgId: sessionUserData.OrgId,
                UserId: sessionUserData.Id,
                ContractorName: formData.ContractorName,
                ShiftTypeId: formData.ShiftTypeId,
                RequiredPasses: formData.RequestPasses,
                ValidStartDt: formData.StartDate,
                ValidEndDt: formData.EndDate,
                // Remarks: formData.Remarks,
            };
            console.log(payload)

            const response = await fetch(`${VMS_URL_CONTRACTOR}POSTContractors`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(payload),
            });

            if (response.ok) {
                const data = await response.json();
                console.log(data.ResultData[0].Status, 'ddddd')
                setAddSubmitLoading(false);

                if (data.ResultData[0].Status === 'Success') {
                    Swal.fire({
                        title: "Success",
                        text: "The contractor has been added successfully.",
                        icon: "success",
                    }).then(() => {
                        window.location.reload();
                    });
                } else {
                    Swal.fire({
                        title: "Error",
                        text: data?.[0]?.Message || "There was an error adding the contractor.",
                        icon: "error",
                    });
                }
            } else {
                console.error("Request failed:", response.statusText);
                Swal.fire({
                    title: "Error",
                    text: "Failed to submit request",
                    icon: "error",
                });
            }
        } catch (error) {
            console.error("Error during submission:", error.message);
            Swal.fire({
                title: "Error",
                text: "An unexpected error occurred",
                icon: "error",
            });
        } finally {
            setAddSubmitLoading(false);
        }
    };

    return (
        <div
            className="offcanvas offcanvas-end"
            tabIndex="-1"
            id="offcanvasRightAdd"
            aria-labelledby="offcanvasRightLabel"
            style={{ minWidth: "45%", maxWidth: "45%" }}
        >
            <form onSubmit={handleSubmit}>
                <div className="offcanvas-header d-flex justify-content-between align-items-center">
                    <h5 id="offcanvasRightLabel" className="mb-0">Add Contractor</h5>
                    <div className="d-flex align-items-center">
                        <button className="btn btn-primary me-2" type="submit" disabled={addSubmitLoading}>
                            {addSubmitLoading ? "Submitting..." : "Submit"}
                        </button>
                        <button
                            type="button"
                            className="btn-close"
                            data-bs-dismiss="offcanvas"
                            aria-label="Close"
                        ></button>
                    </div>
                </div>
                <div className="offcanvas-body" style={{ marginTop: "-2rem", maxHeight: "42rem", overflowY: "auto" }}>
                    <div className="row">
                        <div className="col-6 mb-2">
                            <label className="form-label">Name<span className="text-danger">*</span></label>
                            <input
                                type="text"
                                name="ContractorName"
                                className="form-control"
                                placeholder="Enter contractor name"
                                value={formData.ContractorName}
                                onChange={handleContactorChange}
                                required
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label">Shifts<span className="text-danger">*</span></label>
                            <select
                                className="form-select"
                                name="ShiftTypeId"
                                value={formData.ShiftTypeId}
                                onChange={handleContactorChange}
                                // required
                            >
                                <option value="">Choose Shift</option>
                                {shiftsData.map((item, index) => (
                                    <option key={index} value={item.Id}>
                                        {item.ShiftName}
                                    </option>
                                ))}
                            </select>
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label">Start Date<span className="text-danger">*</span></label>
                            <input
                                type="date"
                                name="StartDate"
                                className="form-control"
                                value={formData.StartDate}
                                onChange={handleContactorChange}
                                required
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label">End Date<span className="text-danger">*</span></label>
                            <input
                                type="date"
                                name="EndDate"
                                className="form-control"
                                value={formData.EndDate}
                                onChange={handleContactorChange}
                                required
                            />
                        </div>
                        <div className="col-6 mb-2">
                            <label className="form-label">Request Passes<span className="text-danger">*</span></label>
                            <input
                                type="number"
                                name="RequestPasses"
                                className="form-control"
                                placeholder="Enter number of passes"
                                value={formData.RequestPasses}
                                onChange={handleContactorChange}
                                required
                            />
                        </div>
                        <div className="col-12 mb-2">
                            <label className="form-label">Remarks</label>
                            <textarea
                                name="Remarks"
                                className="form-control"
                                rows={3}
                                placeholder="Enter notes"
                                value={formData.Remarks}
                                onChange={handleContactorChange}
                            ></textarea>
                        </div>
                    </div>
                </div>
            </form>
        </div>
    )
}