
import React, { useState, useEffect, useRef } from "react";
import Base from "../Config/Base";
import { Popover } from 'antd';
import { VMS_URL } from "../Config/Config";
import ViewVisit from "./View";
import '../Config/Pagination.css';
import AddVisit from "./Add";
import Swal from 'sweetalert2';
import QRCode from 'qrcode'; 
import PrintPass from './PrintPass';
import EditPass from "./Edit";
import PassCheckIn from "./PassCheckIn";


export default function VisitingList () {

    const [sessionUserData, setSessionUserData] = useState([]);
    const [visitorsData, setVistorsData] = useState([]);
    const [dataLoading, setDataLoading] = useState(false);
    const [viewDataId, setViewDataId] = useState([]);
    const [editData, setEditData] = useState([]);
    const [searchQuery, setSearchQuery] = useState("");
    const [qrCodeUrl, setQrCodeUrl] = useState(null);
    const [printData, setPrintData] = useState([]);
    const printRef = useRef();

    useEffect(() => {
        const userDataString = sessionStorage.getItem('userData');
        if (userDataString) {
            const userData = JSON.parse(userDataString);
            setSessionUserData(userData);
        } else {
            console.log('User data not found in sessionStorage');
        }
    }, []);

    const fetchData = async () => {
        setDataLoading(true);
        try {
            const response = await fetch(`${VMS_URL}getReqPass?OrgId=${sessionUserData.OrgId}`);
            if (response.ok) {
                const data = await response.json();
                setVistorsData(data.ResultData);
            } else {
                console.error('Failed to fetch attendance data:', response.statusText);
            }
        } catch (error) {
            console.error('Error fetching attendance data:', error.message);
        } finally {
            setDataLoading(false);
        }
    };

    useEffect(() => {
        if (sessionUserData.OrgId) {
            fetchData();
        }
    }, [sessionUserData]);

    const formatDate = (dateString) => {
        const date = new Date(dateString);
        const formattedDate = date.toLocaleDateString('en-GB');
        const formattedTime = date.toLocaleTimeString('en-GB', {
            hour: '2-digit',
            minute: '2-digit',
        });
        return `${formattedDate} ${formattedTime}`;
    };

    const filteredData = visitorsData.filter((item) => {
        const visitorName = item?.VisitorName?.toLowerCase() || '';
        const status = item?.Status?.toLowerCase() || '';
        const autoIncId = item?.AutoIncNo?.toString() || '';
        const mobile = item?.Mobile?.toString() || '';
    
        const query = searchQuery.toLowerCase();
    
        return (
            visitorName.includes(query) ||
            status.includes(query) ||
            autoIncId.includes(searchQuery) ||
            mobile.includes(searchQuery)      
        );
    });

    const [currentPage, setCurrentPage] = useState(1);
    const recordsPerPage = 5;
    const totalPages = Math.ceil(filteredData.length / recordsPerPage);

    // Get current records to display
    const indexOfLastRecord = currentPage * recordsPerPage;
    const indexOfFirstRecord = indexOfLastRecord - recordsPerPage;
    const currentRecords = filteredData.slice(indexOfFirstRecord, indexOfLastRecord);

    const getPaginationNumbers = () => {
        const visiblePages = [];
        if (totalPages <= 6) {
            // Display all pages if total pages are <= 6
            for (let i = 1; i <= totalPages; i++) visiblePages.push(i);
        } else {
            // Start pages
            if (currentPage <= 3) {
                visiblePages.push(1, 2, 3, "...", totalPages - 2, totalPages - 1, totalPages);
            }
            // Middle pages
            else if (currentPage > 3 && currentPage < totalPages - 2) {
                visiblePages.push(1, 2, "...", currentPage - 1, currentPage, currentPage + 1, "...", totalPages - 1, totalPages);
            }
            // End pages
            else {
                visiblePages.push(1, 2, "...", totalPages - 3, totalPages - 2, totalPages - 1, totalPages);
            }
        }
        return visiblePages;
    };

    const handlePageClick = (page) => {
        if (page !== "...") setCurrentPage(page);
    };

    const handlePrevious = () => {
        if (currentPage > 1) setCurrentPage(currentPage - 1);
    };

    const handleNext = () => {
        if (currentPage < totalPages) setCurrentPage(currentPage + 1);
    };

    const handleView = (item) => {
        setViewDataId(item.RequestId);
    };

    const handleEdit = (item) => {
        setEditData(item);
    };

    const handlePrintVisit = async (item) => {
        setPrintData(item);
    
        const qrData = `${item.AutoIncNo}`;
    
        try {
            const qrCodeDataUrl = await QRCode.toDataURL(qrData);
            setQrCodeUrl(qrCodeDataUrl);
    
            setTimeout(() => {
                if (printRef.current) {
                    const printWindow = window.open('', '_blank');
    
                    printWindow.document.write(`
                        <html>
                            <head>
                                <title>Print</title>
                                <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/bootstrap/5.3.0/css/bootstrap.min.css" />
                                <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.7.0/css/all.min.css" />
                            </head>
                            <body>
                                <div>${printRef.current.innerHTML}</div>
                            </body>
                        </html>
                    `);
    
                    printWindow.document.close();
    
                    printWindow.onload = () => {
                        printWindow.focus();
                        printWindow.print();
                        printWindow.close();
                    };
                } else {
                    console.error('printRef.current is undefined');
                }
            }, 100); // Add a small delay to ensure DOM updates are applied
        } catch (error) {
            console.error('Error generating QR code:', error);
        }
    };
    

    const handleAction = (item, action) => {
        const actionText = action === 'accept' ? 'accept' : 'reject';
        const confirmButtonText = action === 'accept' ? 'Yes, accept it!' : 'Yes, reject it!';
    
        Swal.fire({
            title: `Are you sure?`,
            text: `Do you want to ${actionText} this item?`,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: confirmButtonText
        }).then((result) => {
            if (result.isConfirmed) {
                // setAcceptFormData(item);
                handleActionSubmit(item, action);
            }
        });
    };

    const handleActionSubmit = async (item, action) => {
        const statusText = action === 'accept' ? 'APPROVE' : 'REJECT';
        
        const payload = {
            "RequestId": item.RequestId,
            "Status": statusText,
            "UserId": sessionUserData.Id,
            "to": item.Email,
            "subject": `Your Pass Request ${statusText}`,
            "text": `
                Dear ${item.VisitorName},
        
                We would like to inform you that your pass request (Request ID: ${item.RequestId}) has been ${statusText.toLowerCase()}.
        
                ${statusText === 'APPROVE' ? 
                    'Congratulations! Your pass request has been approved. You can now visit the premises as per the guidelines provided.' : 
                    'Unfortunately, your pass request has been rejected. Please feel free to reach out if you have any questions or require further assistance.'
                }
        
                For more details, you can visit your request page using the link below:
                http://vmsdemo1.s3-website.eu-north-1.amazonaws.com/${item.RequestId}
        
                Best regards,
                Visitor Management System (VMS)
            `,
            "html": `
                <h1>${statusText === 'APPROVE' ? 'Congratulations!' : 'Notice'}</h1>
                <p>Dear ${item.VisitorName},</p>
                <p>We would like to inform you that your pass request (Request ID: ${item.AutoIncNo}) has been ${statusText.toLowerCase()}.</p>
                <p>
                    ${statusText === 'APPROVE' ? 
                        'Congratulations! Your pass request has been approved. You can now visit the premises as per the guidelines provided.' : 
                        'Unfortunately, your pass request has been rejected. Please feel free to reach out if you have any questions or require further assistance.'
                    }
                </p>
                <p>
                    For more details, you can visit your request page using the link below:
                    <br>
                    <a href="http://vmsdemo1.s3-website.eu-north-1.amazonaws.com/${item.RequestId}">View Your Request</a>
                </p>
                <p>Best regards,<br>Copper Wind Industry</p>
            `
        };
        
        console.log(payload, 'payloadpayload')
    
        try {
            const response = await fetch(`${VMS_URL}PassApproval&Email`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(payload)
            });

            const result = await response.json();
            console.log(result)
            
            if (response.ok) {
                if (result.Status) {
                    fetchData();
                }
                throw new Error("Request failed with status " + response.status);
            }
            // Handle success response if needed
            console.log("Request successful:", await response.json());
        } catch (error) {
            console.error("Error submitting action:", error);
        }
    };

   

    return (
        <Base>
            {/* <div id="kt_app_toolbar" className="app-toolbar py-3 pb-lg-4">
                <div id="kt_app_toolbar_container" className="app-container container-xxl d-flex flex-stack">
                    <div className="page-title d-flex flex-column justify-content-center flex-wrap me-3">
                        <h1 className="page-heading d-flex text-gray-900 fw-bold fs-3 flex-column justify-content-center my-0">Visitors</h1>
                        <ul className="breadcrumb breadcrumb-separatorless fw-semibold fs-7 my-0 pt-1">
                            <li className="breadcrumb-item text-muted">
                                <a href="index.html" className="text-muted text-hover-primary">Home</a>
                            </li>
                            <li className="breadcrumb-item">
                                <span className="bullet bg-gray-500 w-5px h-2px"></span>
                            </li>
                            <li className="breadcrumb-item text-muted">Visitors</li>
                        </ul>
                    </div>
                    <div className="d-flex align-items-center gap-2 gap-lg-3">
                        <div data-kt-daterangepicker="true" data-kt-daterangepicker-opens="left" className="btn btn-sm fw-bold btn-secondary d-flex align-items-center px-4">
                            <div className="text-gray-600 fw-bold">Loading date range...</div>
                            <i className="ki-duotone ki-calendar-8 fs-2 ms-2 me-0">
                                <span className="path1"></span>
                                <span className="path2"></span>
                                <span className="path3"></span>
                                <span className="path4"></span>
                                <span className="path5"></span>
                                <span className="path6"></span>
                            </i>
                        </div>
                        <a href="#" className="btn btn-sm fw-bold btn-primary" data-bs-toggle="modal" data-bs-target="#kt_modal_new_target">Add Target</a>
                    </div>
                </div>
            </div> */}
            <div id="kt_app_content" className="app-content flex-column-fluid">
                <div id="kt_app_content_container" className="app-container container-xxl">
                    <div className="row gy-10 gx-xl-10"></div>
                    <div className="card">
                        <div className="card-header border-0 pt-6">
                            <div className="card-title">
                                <div className="d-flex align-items-center position-relative my-1">
                                    <i className="ki-duotone ki-magnifier fs-3 position-absolute ms-5">
                                        <span className="path1"></span>
                                        <span className="path2"></span>
                                    </i>
                                    <input
                                        type="text" 
                                        data-kt-customer-table-filter="search" 
                                        className="form-control form-control-solid w-250px ps-13"
                                        placeholder="Search Visitors" 
                                        value={searchQuery}
                                        onChange={(e) => setSearchQuery(e.target.value)}
                                    />
                                </div>
                            </div>
                            <div className="card-toolbar">
                                <div className="d-flex justify-content-end" data-kt-customer-table-toolbar="base">
                                    <div className="w-150px me-3">
                                        <select className="form-select form-select-solid" data-control="select2" data-hide-search="true" data-placeholder="Status" data-kt-ecommerce-order-filter="status">
                                            <option></option>
                                            <option value="all">All</option>
                                            <option value="active">Active</option>
                                            <option value="locked">Locked</option>
                                        </select>
                                    </div>
                                    <button type="button" className="btn btn-light-primary me-3" 
                                        data-bs-toggle="offcanvas" 
                                        data-bs-target="#offcanvasRightPassCheckIn" 
                                        aria-controls="offcanvasRightPassCheckIn"
                                    >
                                        <i className="ki-duotone ki-exit-up fs-2">
                                            <span className="path1"></span>
                                            <span className="path2"></span>
                                        </i>
                                        Pass Check In/Out
                                    </button>
                                    <button type="button" className="btn btn-primary" data-bs-toggle="offcanvas" 
                                        data-bs-target="#offcanvasRightAdd" 
                                        aria-controls="offcanvasRightAdd">Add</button>
                                </div>
                                <div className="d-flex justify-content-end align-items-center d-none" data-kt-customer-table-toolbar="selected">
                                    <div className="fw-bold me-5">
                                    <span className="me-2" data-kt-customer-table-select="selected_count"></span>Selected</div>
                                    <button type="button" className="btn btn-danger" data-kt-customer-table-select="delete_selected">Delete Selected</button>
                                </div>
                            </div>
                        </div>
                        <div className="card-body pt-0">
                            <table className="table align-middle table-row-dashed fs-6 gy-5" id="kt_customers_table">
                                <thead>
                                    <tr className="text-start text-gray-500 fw-bold fs-7 text-uppercase gs-0">
                                        <th className="">S.No</th>
                                        <th className="min-w-125px">Id</th>
                                        <th className="min-w-125px">Visitor</th>
                                        <th className="min-w-125px">Date</th>
                                        <th className="min-w-125px">Mobile</th>
                                        <th className="min-w-125px">No Of Members</th>
                                        <th className="min-w-125px">Status</th>
                                        <th className="text-end min-w-70px">Actions</th>
                                    </tr>
                                </thead>
                                <tbody className="fw-semibold text-gray-600">
                                    {dataLoading ? <p className="text-center">Loading...</p> : 
                                        currentRecords && currentRecords?.map((item, index) => (
                                            <tr>
                                               <td>{(currentPage - 1) * recordsPerPage + index + 1}</td>
                                                <td>
                                                    <a href="apps/ecommerce/customers/details.html" class="text-gray-800 text-hover-primary mb-1">{item.AutoIncNo}</a>
                                                </td>
                                                <td>
                                                    <a href="#" class="text-gray-600 text-hover-primary mb-1">{item.VisitorName}</a>
                                                </td>
                                                <td>
                                                    <div class="badge badge-light-danger">{formatDate(item.RequestDate)}</div>
                                                </td>
                                                <td>{item.Mobile}</td>
                                                <td>{item.NoOfMembers}</td>
                                                <td>
                                                    <div className={
                                                        item.Status === "DRAFT" ? "badge badge-light-dark" :
                                                        item.Status === "APPROVE" ? "badge badge-light-info" :
                                                        item.Status === "REJECT" ? "badge badge-light-danger" :
                                                        item.Status === "COMPLETED" ? "badge badge-light-success" :
                                                        "badge badge-light"
                                                    }>
                                                        {item.Status}
                                                    </div>
                                                </td>
                                                <td class="text-end">
                                                    <Popover 
                                                        placement="bottom" 
                                                        content={
                                                            <div style={{ width: '8rem' }}>
                                                                <p 
                                                                    style={{ cursor: 'pointer' }}
                                                                    className="text-hover-warning"
                                                                    data-bs-toggle="offcanvas" 
                                                                    data-bs-target="#offcanvasRightView" 
                                                                    aria-controls="offcanvasRightView"    
                                                                    onClick={() => handleView(item)}
                                                                
                                                                ><i className="fa-regular fa-eye me-2"></i>View</p>
                                                                <p 
                                                                    style={{ cursor: 'pointer' }} 
                                                                    className="text-hover-warning"
                                                                    data-bs-toggle="offcanvas" 
                                                                    data-bs-target="#offcanvasRightEdit" 
                                                                    aria-controls="offcanvasRightEdit"    
                                                                    onClick={() => handleEdit(item)}
                                                                >
                                                                    <i className="fa-regular fa-pen-to-square me-2 text-info"></i>
                                                                    Edit
                                                                </p>
                                                                <p
                                                                    style={{
                                                                        cursor: sessionUserData.RoleId === 1 && (item.Status == 'DRAFT' || item.Status == 'REJECT') ? 'pointer' : 'not-allowed',
                                                                        pointerEvents: sessionUserData.RoleId === 1 && (item.Status == 'DRAFT' || item.Status == 'REJECT') ? 'auto' : 'none',
                                                                        opacity: sessionUserData.RoleId === 1 && (item.Status === 'DRAFT' || item.Status === "REJECT") ? 1 : 0.5
                                                                    }}
                                                                    className="text-hover-warning"
                                                                    onClick={() => (sessionUserData.RoleId === 1 && !(item.Status === 1 || item.Status === 3)) ? handleAction(item, 'accept') : null}
                                                                    // onClick={() =>handleAction(item, 'accept')}
                                                                >
                                                                    <i className="fa-solid fa-check me-2 text-success"></i>Accept
                                                                </p>
                                                                <p 
                                                                    style={{
                                                                        cursor: sessionUserData.RoleId === 1 && (item.Status == 'DRAFT' || item.Status == 'APPROVE') ? 'pointer' : 'not-allowed',
                                                                        pointerEvents: sessionUserData.RoleId === 1 && (item.Status == 'DRAFT' || item.Status === 2) ? 'auto' : 'none',
                                                                        opacity: sessionUserData.RoleId === 1 && (item.Status == 'DRAFT' || item.Status == 'APPROVE') ? 1 : 0.5
                                                                    }}
                                                                    className="text-hover-warning"
                                                                    onClick={() => (sessionUserData === 1 && (item.Status === 'DRAFT' || item.Status === 'APPROVE')) ? handleAction(item, 'reject') : null}
                                                                    // onClick={() => handleAction(item, 'reject')}
                                                                >
                                                                    <i className="fa-solid fa-xmark me-2 text-danger"></i>Reject
                                                                </p>
                                                                <p 
                                                                    style={{ cursor: 'pointer' }} 
                                                                    className="text-hover-warning"
                                                                    onClick={() =>handlePrintVisit(item)}
                                                                >
                                                                    <i className="fa-solid fa-print me-2"></i>Print
                                                                </p>
                                                            </div>
                                                        }
                                                        trigger="click"
                                                    >
                                                        <button 
                                                            className="btn"
                                                        >
                                                            <i className="fa-solid fa-ellipsis-vertical"></i>
                                                        </button>
                                                    </Popover>
                                                </td>
                                            </tr>
                                        ))
                                    }
                                </tbody>
                            </table>
                            <div className="dt-paging paging_simple_numbers">
                                <nav aria-label="pagination">
                                    <ul className="pagination">
                                        {/* Previous Button */}
                                        <li
                                            className={`dt-paging-button page-item ${currentPage === 1 ? "disabled" : ""}`}
                                        >
                                            <button
                                                className="page-link previous"
                                                role="link"
                                                type="button"
                                                aria-controls="kt_customers_table"
                                                aria-disabled={currentPage === 1}
                                                aria-label="Previous"
                                                onClick={handlePrevious}
                                            >
                                                <i className="previous"></i>
                                            </button>
                                        </li>

                                        {/* Page Numbers */}
                                        {getPaginationNumbers().map((page, index) => (
                                            <li
                                                key={index}
                                                className={`dt-paging-button page-item ${page === currentPage ? "active" : ""}`}
                                            >
                                                <button
                                                    className="page-link"
                                                    role="link"
                                                    type="button"
                                                    aria-controls="kt_customers_table"
                                                    aria-current={page === currentPage ? "page" : undefined}
                                                    onClick={() => handlePageClick(page)}
                                                    disabled={page === "..."}
                                                >
                                                    {page}
                                                </button>
                                            </li>
                                        ))}

                                        {/* Next Button */}
                                        <li
                                            className={`dt-paging-button page-item ${currentPage === totalPages ? "disabled" : ""}`}
                                        >
                                            <button
                                                className="page-link next"
                                                role="link"
                                                type="button"
                                                aria-controls="kt_customers_table"
                                                aria-disabled={currentPage === totalPages}
                                                aria-label="Next"
                                                onClick={handleNext}
                                            >
                                                <i className="next"></i>
                                            </button>
                                        </li>
                                    </ul>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </div>    

            <ViewVisit viewId={viewDataId} />         
            <AddVisit />  
            <EditPass passObj={editData} />
            <PassCheckIn />

             {/* print */}
             <div className='d-none'>
                {printData ? (
                    <PrintPass ref={printRef} data={printData} qrCodeUrl={qrCodeUrl}/>
                ) : (
                    <p>Loading...</p>
                )}
            </div>                    
        </Base>
    )
}